import React from 'react';
import styled from 'styled-components'

const Wrap = styled.div`
  border: 1px solid black;
  margin-bottom: 100px;
  width: 500px;
  height: 500px;
  position: relative;
  padding: 10px;
`
const Sec_one = styled.div`
  width: 100%;
  height: 70%;
  /* background-color: black; */
  float: left;
  div{
    width:50%;
    float:left;
  }
`
const Sec_two = styled.div`
  width: 100%;
  height: 30%;
  /* background-color: green; */
  float: left;
`

function Movie({id, year, title, summary, poster, genres}) {

  return (
    <Wrap>
      <Sec_one>
        <div>
          <img src={poster} alt={title} title={title}/>
        </div>
        <div>
          <h3>{title}</h3>
          <h4>{year}</h4> 
          {genres.map((arg, index) => {
            return <h5 key={index}>▷{arg}</h5>   
          })
          }
        </div>
      </Sec_one>
      <Sec_two>
        { summary.length >= 500 ? 
          <h5>{summary.slice(0, 500)}...</h5> : 
          <h5>{summary}</h5>
        }
           
      </Sec_two>
    </Wrap>
  );
}

export default Movie;